<template>
	<div class="type-view">
		<!-- 二级分类标题 -->
		<div class="type-two" v-if="typeTwo.length">
			<ul class="type-list">
				<li v-for="(type, index) in typeTwo" :key="index" @click="typeTwoClick(index)" :class="{ active: typeTwoIndex==index, 'type-item': true }">{{ type }}</li>
			</ul>
		</div>
		<!-- 商品列表 -->
		<div class="goods-list">
			<div class="type-box" ref="typeBox" v-for="(item, index) in goodsList" :key="index">
				<div class="type">{{ item.type_two }}</div>
				<div class="goods-box">
					<goods-item v-for="goods in item.list" :key="goods.Id" :goods="goods"></goods-item>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { getGoodsListApi, getTypeTwoApi } from '@/api/goods';
import GoodsItem from '@/components/GoodsItem';

export default {
	name: 'type-view',
	components: {
		GoodsItem,
	},
	data() {
		return {
			typeTwoIndex: 0,
			typeTwo: [],
			goodsList: [],
		};
	},
  created() {
    console.log('----typelist');
  },
	beforeRouteEnter(to, from, next) {
		next(vm => {
			vm.getTypeTwo(to);
			vm.getGoodsList(to);
			document.addEventListener('scroll', vm.docScroll);
		});
	},
	beforeRouteUpdate(to, from, next) {
		this.getTypeTwo(to);
		this.getGoodsList(to);
		next();
	},
	beforeRouteLeave(to, from, next) {
		document.removeEventListener('scroll', this.docScroll);
		next();
	},
	methods: {
		// 获取二级分类标题
		async getTypeTwo(to) {
			let res = await getTypeTwoApi({ type_one: to.params.type });
			this.typeTwo = res;
		},
		// 根据一级分类获取所有商品
		async getGoodsList(to) {
			let res = await getGoodsListApi({ type_one: to.params.type });
			this.goodsList = this.getList(res);
		},
		// 处理所有的商品
		getList(list) {
			var new_list = [];
			list.forEach(item => {
				if (new_list.length == 0) {
					new_list.push({ type_two: item.type_two, list: [item] });
				} else {
					var flag = new_list.some(new_item => {
						if (new_item.type_two == item.type_two) {
							new_item.list.push(item);
							return true;
						}
					});
					if (!flag) {
						new_list.push({ type_two: item.type_two, list: [item] });
					}
				}
			});
			return new_list;
		},

		// getList(list, typeTwo){
		//     var new_list = [];
		//     for(var i = 0; i < typeTwo.length; i++){
		//         var temp_list = []
		//         for (var j = 0; j < list.length; j++){
		//             if (typeTwo[i] == list[j].type_two) {
		//                 temp_list.push(list[j]);
		//             }
		//         }
		//         new_list.push({
		//             type_two:typeTwo[i],
		//             list:temp_list,
		//         });
		//     }
		//     return new_list;
		// },

		typeTwoClick(index) {
			// 获取标签相对于网页顶部的距离,也就是,当标题滚动到窗口顶部时,网页的滚动距离, (网页将要滚动的目标位置) 减80为了预留顶部导航和搜索栏的空间
			var targetTop = this.$refs.typeBox[index].offsetTop - 80; //滚动的目标位置
			// 获取当前网页的滚动距离
			var currentTop = document.scrollingElement.scrollTop;
			var offsetTop = currentTop - targetTop; // 计算网页的滚动总距离
			var speed = offsetTop / 10; //每一帧的滚动距离
			if (speed == 0) return;
			update.call(this);
			function update() {
				currentTop -= speed;
				document.documentElement.scrollTop = currentTop;
				if ((offsetTop > 0 && currentTop > targetTop) || (offsetTop < 0 && currentTop < targetTop)) {
					requestAnimationFrame(update.bind(this));
				} else {
					document.scrollingElement.scrollTop = targetTop;
					this.typeTwoIndex = index;
				}
			}
		},

		docScroll() {
			var index = 0;
			var scrollTop = document.scrollingElement.scrollTop;
			for (var i = 0; i < this.$refs.typeBox.length; i++) {
				if (scrollTop + document.documentElement.clientHeight / 2 > this.$refs.typeBox[i].offsetTop) {
					index = i;
				}
			}
			this.typeTwoIndex = index;
		},
	},
};
</script>

<style lang="scss" scoped>
// 二级分类标题
.type-two {
	width: 120px;
	position: fixed;
	right: calc(50% + 480px + 20px);
	border: 1px solid #aaa;
	text-align: left;
	padding: 5px 0;
	border-radius: 10px;
	overflow: hidden;
	.type-item {
		padding-left: 10px;
		margin: 5px 0;
		cursor: pointer;
		position: relative;
	}
	.type-item.active,
	.type-item:hover {
		color: #800088;
		font-weight: 700;
	}
	.type-item::before {
		content: '';
		height: 0;
		width: 2px;
		background-color: #800088;
		position: absolute;
		top: 0;
		left: 5px;
		transition: 0.3s linear;
	}
	.type-item.active::before {
		height: 100%;
	}
}
// 商品列表
.goods-list {
	.type-box .type {
		background-color: #f0f8ff;
		font-weight: 700;
		text-align: center;
		line-height: 50px;
		margin: 20px 0;
		font-size: 20px;
	}
	.goods-box {
		margin: 0 -10px;
		display: flex;
		flex-wrap: wrap;
	}
}
</style>
